}
.food .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.food .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
