  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.food .rating-wrapper .rating-item .user .name {
  font-size: 10px;
  color: #93999f;
  margin-right: 6px;
}
.food .rating-wrapper .rating-item .user .avatar {
