  color: #93999f;
}
.food .rating-wrapper .rating-item .text {
  line-height: 16px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.food .rating-wrapper .rating-item .text .icon-support {
  font-size: 10px;
  margin-right: 4px;
  color: #00a0c8;
}
.food .rating-wrapper .rating-item .text .icon-cai {
  font-size: 10px;
  margin-right: 4px;
  color: #93999f;
}
.food .rating-wrapper .rating-item .text .txt {
  font-size: 12px;
}


