
.ratingselect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.ratingselect .rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.ratingselect .rating-type .block {
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: #4d555d;
  font-size: 12px;
}
.ratingselect .rating-type .block.active {
  color: #fff;
}
.ratingselect .rating-type .block .count {
  margin-left: 2px;
  font-size: 8px;
}
.ratingselect .rating-type .block.positive {
  background-color: rgba(0,160,220,0.2);
}
.ratingselect .rating-type .block.positive.active {
  background-color: #00a0dc;
}
.ratingselect .rating-type .block.negative {
  background-color: rgba(77,85,93,0.2);
}
.ratingselect .rating-type .block.negative.active {
  background-color: #4d555d;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-times: center;
}
.ratingselect .switch.on .iconfont {
  color: #00c850;
}
.ratingselect .switch .iconfont {
  font-szie: 24px;
  margin-right: 4px;
  margin-top: 1px;
}
.ratingselect .switch .text {
  font-size: 12px;
}


